'use client';

import Image from 'next/image';
import { useParams } from 'next/navigation';

/* ------------------------------- mock data ------------------------------- */
type Skill = { id: string; name: string; level: number; type: 'soft' | 'hard' };

type Activity = {
  id: string;
  title: string;
  joinedAt: string;
  skillsGained: { name: string; level: number }[];
};

type StudentProfile = {
  id: string;
  fullName: string;
  studentId: string;
  advisor: string;
  major: string;
  faculty: string;
  softSkills: Skill[];
  hardSkills: Skill[];
  activities: Activity[];
};

const mockProfile: StudentProfile = {
  id: '123',
  fullName: 'ธนิน เกิดทิพย์',
  studentId: '66XXXXXXX',
  advisor: 'ผศ. ดร. สมชาย ใจดี',
  major: 'สาขาวิทยาการคอมพิวเตอร์',
  faculty: 'วิทยาศาสตร์และเทคโนโลยี',
  softSkills: [
    { id: 's1', name: 'การสื่อสาร (Communication)', level: 3, type: 'soft' },
    { id: 's2', name: 'การทำงานเป็นทีม (Teamwork)', level: 3, type: 'soft' },
    { id: 's3', name: 'การบริหารเวลา (Time Management)', level: 1, type: 'soft' },
  ],
  hardSkills: [
    {
      id: 'h1',
      name: 'การเขียนโปรแกรมภาษา Python (Python Programming)',
      level: 1,
      type: 'hard',
    },
    {
      id: 'h2',
      name: 'การใช้โปรแกรม Photoshop (Photoshop Skills)',
      level: 3,
      type: 'hard',
    },
    { id: 'h3', name: 'การออกแบบเว็บไซต์ (Web Design)', level: 2, type: 'hard' },
  ],
  activities: [
    {
      id: 'act1',
      title: 'ชื่อกิจกรรม',
      joinedAt: '12 มิ.ย. 2568',
      skillsGained: [
        { name: 'Python', level: 2 },
        { name: 'Communication', level: 1 },
      ],
    },
  ],
};
/* ------------------------------------------------------------------------ */

const Badge = ({ level }: { level: number }) => (
  <span className="ml-2 rounded bg-gray-200 px-1.5 text-xs font-semibold text-gray-700">
    {level}
  </span>
);

export default function StudentProfilePage() {
  const { studentId } = useParams(); // ใช้ param ถ้าต้องดึงจาก API
  const p = mockProfile; // ในอนาคตสลับเป็น fetch ด้วย studentId

  return (
    <div className="mx-auto max-w-6xl px-4 py-6 space-y-6">
      {/* heading */}
      <h1 className="text-2xl font-bold">โปรไฟล์ของฉัน</h1>

      {/* personal + skill section */}
      <div className="grid gap-6 md:grid-cols-3">
        {/* personal info */}
        <section className="rounded-lg border bg-white p-6 shadow-sm md:col-span-1">
          <h2 className="mb-4 text-lg font-semibold">ข้อมูลส่วนตัว</h2>
          <ul className="space-y-1 text-sm leading-6">
            <li>ชื่อ-นามสกุล: {p.fullName}</li>
            <li>รหัสนักศึกษา: {p.studentId}</li>
            <li>อาจารย์ที่ปรึกษา: {p.advisor}</li>
            <li>สาขาวิชา: {p.major}</li>
            <li>คณะ: {p.faculty}</li>
          </ul>
        </section>

        {/* skills */}
        <section className="rounded-lg border bg-white p-6 shadow-sm md:col-span-2">
          <h2 className="mb-4 text-lg font-semibold">ทักษะที่มี</h2>
          <div className="grid gap-4 md:grid-cols-2">
            {/* soft */}
            <div>
              <h3 className="text-base font-bold">Soft Skills</h3>
              <p className="mb-2 text-sm text-gray-500">
                ทักษะทางอารมณ์และสังคม
              </p>
              <ul className="space-y-1 text-sm">
                {p.softSkills.map(s => (
                  <li key={s.id}>
                    • {s.name}
                    <Badge level={s.level} />
                  </li>
                ))}
              </ul>
            </div>
            {/* divider on md screens handled by grid gap */}

            {/* hard */}
            <div className="md:border-l md:pl-6">
              <h3 className="text-base font-bold">Hard Skills</h3>
              <p className="mb-2 text-sm text-gray-500">ทักษะทางเทคนิค</p>
              <ul className="space-y-1 text-sm">
                {p.hardSkills.map(s => (
                  <li key={s.id}>
                    • {s.name}
                    <Badge level={s.level} />
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </section>
      </div>

      {/* activities */}
      <section>
        <h2 className="mb-4 text-lg font-semibold">กิจกรรมที่เคยเข้าร่วม</h2>
        <div className="grid gap-4 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
          {p.activities.map(a => (
            <article
              key={a.id}
              className="flex flex-col overflow-hidden rounded-lg border bg-white shadow-sm"
            >
              {/* ใช้รูปจริงในอนาคต */}
              <div className="relative h-32 w-full bg-gray-100">
                <Image
                  src="/placeholder.svg"
                  fill
                  alt="activity"
                  className="object-cover"
                />
              </div>
              <div className="p-4 space-y-2">
                <h4 className="text-sm font-semibold">{a.title}</h4>
                <p className="text-xs text-gray-500">วัน-เวลาที่เข้าร่วม: {a.joinedAt}</p>
                <p className="text-xs">ทักษะที่ได้รับ :</p>
                <ul className="flex flex-wrap gap-1">
                  {a.skillsGained.map(s => (
                    <li
                      key={s.name}
                      className="rounded bg-gray-200 px-1.5 text-[10px] font-medium text-gray-700"
                    >
                      {s.name} : {s.level}
                    </li>
                  ))}
                </ul>
              </div>
            </article>
          ))}
        </div>
      </section>
    </div>
  );
}
